@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1080px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 900px !important;
  }
}
@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1200px !important;
  }
  .container.c-blog,
  .container-lg.c-blog,
  .container-md.c-blog,
  .container-sm.c-blog,
  .container-xl.c-blog,
  .container-xxl.c-blog {
    max-width: 1000px !important;
  }
}
#incorporadora h2 {
  font-size: 16px !important;
  font-weight: 400;
}
#incorporadora p {
  font-size: 26px;
  font-weight: 500;
  margin: 0;
}

#portfolio .container-fluid {
  border-bottom: 2px solid #999999;
  margin-bottom: 20px;
}
#portfolio #portfolio-posts {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
}

#video-institucional {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 40px 0;
}
#video-institucional span {
  display: flex;
  margin-bottom: 10px;
  font-size: 14px;
  color: #fff;
}
#video-institucional h2 {
  color: #fff;
}
#video-institucional h2.sec-titulo {
  padding-bottom: 15px;
  margin-bottom: 30px;
  border-bottom: 1px solid #fff;
}
#video-institucional p {
  color: #fff;
}

#blog-footer {
  background-color: #404040;
  margin: 0;
  padding-top: 40px;
}
#blog-footer h2,
#blog-footer p {
  color: #fff;
}
#blog-footer .blog-posts {
  margin-top: 30px;
  padding-bottom: 30px;
}
#blog-footer .blog-posts .post {
  transition: all ease 0.3s;
}
#blog-footer .blog-posts .post a {
  text-decoration: none;
  color: #fff;
}
#blog-footer .blog-posts .post a .post-thumbnail {
  margin-bottom: 10px;
}
#blog-footer .blog-posts .post a .post-thumbnail img {
  width: 100%;
  height: auto;
}
#blog-footer .blog-posts .post a h3 {
  font-size: 16px;
  margin: 15px 0 10px 0;
}
#blog-footer .blog-posts .post a p {
  font-size: 12px;
  color: #fff;
  margin-bottom: 0;
}
#blog-footer .blog-posts .post:nth-child(1) {
  padding-right: 45px;
}
#blog-footer .blog-posts .post:nth-child(2) {
  padding: 0 30px;
}
#blog-footer .blog-posts .post:nth-child(3) {
  padding-left: 45px;
}
#blog-footer .blog-posts .post.not-active {
  opacity: 0.4;
}
#blog-footer .blog-posts .post.active {
  opacity: 1;
}